import classNames from "classnames";
import styles from "./step.module.css";
import { base_url } from "@/utils/const";
import React, { useEffect, useMemo, useState } from "react";
import { getBrowserLang } from "@/utils";
import { useI18n } from "@/hooks";

export type StepConfig = { step: number, name: string, href: string }

type LangType = {
  step1: string,
  step2: string,
  step3: string
}
const langs: { [key: string]: LangType } = {
  default: {
    step1: "",
    step2: "",
    step3: ""
  },
  zh: {
    step1: "Âõ∫ÂÆöÊèí‰ª∂",
    step2: "ÁøªËØëÁΩëÈ°µ",
    step3: "ÂºÄÂßã‰ΩøÁî®"
  },
  en: {
    step1: "Fixed plugin",
    step2: "Translate webpage",
    step3: "Get Started"
  }
}
export default function Step({ step, configs }: { step: number, configs?: StepConfig[] }) {
  const i18n = useI18n<LangType>(langs);
  const defaultConfigs: StepConfig[] = [
    { step: 1, name: i18n["step1"], href: base_url + "/" },
    { step: 2, name: i18n["step2"], href: base_url + "/step-2/" },
    { step: 3, name: i18n["step3"], href: base_url + "/step-3/" },
  ];
  const curConfigs = configs || defaultConfigs;

  return (
    <div className={classNames(styles.container, "notranslate")}>
      {
        curConfigs.map((item, index) => {
          const isActive = item.step == step;
          return (
            <React.Fragment key={item.step}>
              <a className={classNames({
                [styles["step"]]: true,
                [styles["step-active"]]: isActive,
              })}
                href={item.href}
              >
                <div
                  className={classNames({
                    [styles["step-circle"]]: true,
                    [styles["step-circle-active"]]: isActive,
                  })}>
                  {item.step}
                </div>
                <span className={styles.label}>{item.name}</span>
              </a>
              {index < curConfigs.length - 1 && <div className={classNames({
                [styles.line]: true,
                [styles.lineActive]: isActive,
              })} />}
            </React.Fragment>
          );
        })
      }
    </div>
  );
}
