import { getBrowserLang } from "@/utils";
import { useEffect, useMemo, useState } from "react";

export function useI18n<T>(langs: any): T {
  const lang = useLang();
  const i18n = useMemo(() => langs[lang || "default"], [lang]);
  return i18n;
}

export function useLang() {
  const [lang, setLang] = useState<Partial<LangEnum>>();
  useEffect(() => {
    setLang(getBrowserLang());
  }, [setLang]);
  return lang;
}